import { mapState,mapGetters } from 'vuex';
export default {
	data() {
		return {
			person_list:[]
		}
	},
	
	components:{
	},
	onLoad() {
		uni.$on('refreshList', () => {
			console.log('refreshList');
			this.get_people_list()
		});
		uni.$on('finshRate', () => {
			console.log('finshRate');
			this.get_people_list()
		});
		this.get_people_list()
	},
	onUnload() {
		uni.$off('refreshJwt');
		uni.$off('finshRate');
	},
	mounted() {
		// this.get_people_list()
	},
	computed: {
		...mapState(['bgt_c_task','bgt_ct_id']),
		...mapGetters(['user_role','id']),
	},
	methods: {
		get_people_list(){
			this.$http.get('personwx.headpersonlist/1.0/',{
				proId:this.bgt_ct_id,
				pid:this.id
			}).then(res =>{
				console.log(res)
				this.person_list = this.$utils._get(res,'data.data',[])
			})
		},
		status_text(status){
			switch (status){
				case '0':
					return '确认不来了'
					break;
				case '1':
					return '待确认'
					break;	
				case '2':
					return '确认工作'
					break;	
				case '3':
					return '下工'
					break;	
				default:
					break;
			}
		},
		status_color(status){
			switch (status){
				case '0':
					return 'line-red'
					break;
				case '1':
					return 'line-blue'
					break;	
				case '2':
					return 'line-green'
					break;	
				case '3':
					return 'line-gray'
					break;	
				default:
					break;
			}
		},
		goto_detail(item){
			uni.navigateTo({
				url: `/pages/tabbar/workbench/ratingDetail/ratingDetail?item=` + encodeURIComponent(JSON.stringify(item))
			});
		},
		format_rating(rating){
			return this.$_.ceil(rating, 2);
		}
	}
}
